	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p><em>Hello, I Am</em> reflects the way I and many others were raised surrounded by two different cultures through photography and story-telling. The photos within the book portray the duality of Asian American identity through the re-contextualization of recognized American food brands or foods associated with America by pairing them with what can be considered its Asian equivalent.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 150.03750937734435%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"dsc 0086\"\n        title=\"\"\n        src=\"/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-755ae.jpg\"\n        srcset=\"/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-f07fc.jpg 125w,\n/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-9d06f.jpg 250w,\n/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-755ae.jpg 500w,\n/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-63ca3.jpg 750w,\n/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-9b9a3.jpg 1000w,\n/static/dsc_0086-149b0eca2e66cc8b282b5c15bfba1868-2ce76.jpg 1333w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.4%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 2841\"\n        title=\"\"\n        src=\"/static/img_2841-f440452a9f86233e438581c24e67a629-755ae.jpg\"\n        srcset=\"/static/img_2841-f440452a9f86233e438581c24e67a629-f07fc.jpg 125w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-9d06f.jpg 250w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-755ae.jpg 500w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-63ca3.jpg 750w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-9b9a3.jpg 1000w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-6e031.jpg 1500w,\n/static/img_2841-f440452a9f86233e438581c24e67a629-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 150.03750937734435%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 3100\"\n        title=\"\"\n        src=\"/static/img_3100-bbb0ab12a44ca5ec420250b943405784-755ae.jpg\"\n        srcset=\"/static/img_3100-bbb0ab12a44ca5ec420250b943405784-f07fc.jpg 125w,\n/static/img_3100-bbb0ab12a44ca5ec420250b943405784-9d06f.jpg 250w,\n/static/img_3100-bbb0ab12a44ca5ec420250b943405784-755ae.jpg 500w,\n/static/img_3100-bbb0ab12a44ca5ec420250b943405784-63ca3.jpg 750w,\n/static/img_3100-bbb0ab12a44ca5ec420250b943405784-9b9a3.jpg 1000w,\n/static/img_3100-bbb0ab12a44ca5ec420250b943405784-2ce76.jpg 1333w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>The book uses a system using stickers was developed in order to allow individuals to interact with the physical piece as well as it conceptually reflects the idea that we label others. The stickers are also used to capture small pull quotes from the stories collected from others about their personal experiences regarding identity.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"laura img2\"\n        title=\"\"\n        src=\"/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-755ae.jpg\"\n        srcset=\"/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-f07fc.jpg 125w,\n/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-9d06f.jpg 250w,\n/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-755ae.jpg 500w,\n/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-63ca3.jpg 750w,\n/static/laura_img2-a601c9f6f6069e496e6ba9f1c23db35e-99f9a.jpg 864w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Additionally, the website exhibits videos of the juxtaposed compositions being used and interacted with as well as information about the project and historical information about the food that was used. The aim of the website is to inform those on the history of Chinese American food as well as breathe life and bring movement to each of the photos that were taken. The background of the website is composed of all of the items purchased in order to produce the compositions, essentially mimicking a receipt, while the foreground mimics the layout of a menu.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"thefinalquestionmark 052\"\n        title=\"\"\n        src=\"/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-755ae.jpeg\"\n        srcset=\"/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-f07fc.jpeg 125w,\n/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-9d06f.jpeg 250w,\n/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-755ae.jpeg 500w,\n/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-63ca3.jpeg 750w,\n/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-9b9a3.jpeg 1000w,\n/static/thefinalquestionmark.052-1dab6e2976171f392734dab90f4af092-11088.jpeg 1024w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>","frontmatter":{"title":"Hello, I Am","graduate":"Laura Pitt","category":"Book Design","category2":"UI/UX","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/thefinalquestionmark.031-82c4603b2028b09c0d38f41b89856f8e-f1245.jpeg","srcSet":"/static/thefinalquestionmark.031-82c4603b2028b09c0d38f41b89856f8e-bac3d.jpeg 375w,\n/static/thefinalquestionmark.031-82c4603b2028b09c0d38f41b89856f8e-11395.jpeg 750w,\n/static/thefinalquestionmark.031-82c4603b2028b09c0d38f41b89856f8e-f1245.jpeg 1024w","sizes":"(max-width: 1024px) 100vw, 1024px"}}}}}},"pathContext":{"slug":"/work/hello-i-am/","name":"/Hello, I Am/"}}

